
.block {
  padding: 30px;
  border: 1px solid #004065;
  height: 100%;
}

.block.h_collaple {
  width: 55px !important;
  padding: 30px 0 !important;
  flex: none !important;
}

.block.v_collaple .block__header {
  margin-bottom: 0;
}

.block.h_collaple .block__header {
  flex-direction: column-reverse;
  margin-bottom: 0;
}

.block.h_collaple .block__title {
  writing-mode: vertical-lr;
  transform: rotate(180deg);
  margin-top: 15px;
  /* margin-right: 20px; */
}

.block__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 40px;
}

.block__title {
  font-size: 16px;
  font-weight: 500;
}

.block__actions {
  display: flex;
  align-items: center;
}

.block__actions > button {
  background-color: transparent;
  border: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  padding: 0;
}

.block__actions > button:hover > svg {
  fill: #cecfd0;
}

.block__actions > button > svg {
  fill: #d9dcde;
  width: 20px;
  height: 20px;
  flex: none;
}

.block__actions > * + * {
  margin-left: 10px;
}

.block__actions .vCollapseIcon {
  transform: rotate(90deg);
}